import { jsOptions, equals } from "../../.fable/fable-library.3.1.15/Util.js";
import { interpolate, toConsole, endsWith } from "../../.fable/fable-library.3.1.15/String.js";
import lodash from "lodash.clonedeep";
import { ofArray, toArray } from "../../.fable/fable-library.3.1.15/List.js";
import { singleton } from "../../.fable/fable-library.3.1.15/AsyncBuilder.js";
import { sleep } from "../../.fable/fable-library.3.1.15/Async.js";

export const JS_deviceInfo = (() => {
    const userAgent = equals(window.navigator, null) ? "" : window.navigator.userAgent;
    let deviceInfo;
    const IsEdge = (new RegExp("Edg\\/")).test(userAgent);
    const IsMobile = (new RegExp("Android|BlackBerry|iPhone|iPad|iPod|Opera Mini|IEMobile|WPDesktop", (('') + "i"))).test(userAgent);
    const IsExtension = window.location.protocol === "chrome-extension:";
    deviceInfo = {
        GitHubPages: endsWith(window.location.host, "github.io"),
        IsEdge: IsEdge,
        IsExtension: IsExtension,
        IsMobile: IsMobile,
    };
    toConsole(interpolate("userAgent=%P() deviceInfo=%P()", [userAgent, deviceInfo]));
    return deviceInfo;
})();

export const JS_isTesting = (process.env.JEST_WORKER_ID) ? true : (!equals(window.Cypress, null));

export function JS_newObj(fn) {
    return jsOptions(fn);
}

export const JS_cloneDeep = lodash;

export function JS_cloneObj(obj, fn) {
    return fn(JS_cloneDeep(obj));
}

export function JS_toJsArray(a) {
    return toArray(ofArray(a));
}

export function JS_waitFor(fn) {
    return singleton.Delay(() => {
        if (fn()) {
            return singleton.Return();
        }
        else {
            if (((!JS_deviceInfo.GitHubPages) ? (!JS_deviceInfo.IsMobile) : false) ? (!JS_isTesting) : false) {
                toConsole(interpolate("[log] %P()", ["waitForObject: null. waiting..."]));
            }
            return singleton.Bind(sleep(100), () => singleton.ReturnFrom(JS_waitFor(fn)));
        }
    });
}

export function JS_waitForObject(fn) {
    return singleton.Delay(() => {
        const obj = fn();
        if (!equals(obj, null)) {
            return singleton.Return(obj);
        }
        else {
            if (((!JS_deviceInfo.GitHubPages) ? (!JS_deviceInfo.IsMobile) : false) ? (!JS_isTesting) : false) {
                toConsole(interpolate("[log] %P()", ["waitForObject: null. waiting..."]));
            }
            return singleton.Bind(sleep(100), () => singleton.ReturnFrom(JS_waitForObject(fn)));
        }
    });
}

export function JS_ofObjDefault(def, obj) {
    if (equals(obj, null) ? true : (((typeof obj) === "object") ? (Object.keys(obj).length === 0) : false)) {
        return def;
    }
    else {
        return obj;
    }
}

